import { Component, Prop, h, Host, Element } from '@stencil/core';
import { classlist } from '../../../utils/classlist';

const variantsConfig = {
    variants: {
        active: {
            true: 'bg-sidebar-primary text-sidebar-primary-foreground',
            false: 'text-sidebar-foreground',
        },
    },
};

const focusIndicatorVariantsConfig = {
    variants: {
        active: {
            true: 'group-hover:bg-blue-900/30 ',
            false: 'group-hover:bg-slate-400/10',
        },
    },
};

/**
 * @category Navigation
 * @description A sidebar menu item component for the sidebar.
 */

@Component({
    tag: 'at-sidebar-menuitem',
    shadow: false,
    styleUrl: 'at-sidebar-menuitem.scss',
})
export class AtSidebarMenuitemComponent {
    /**
     * Label to be displayed for the menu item
     */
    @Prop() label?: string;

    /**
     * Icon to be displayed with the label
     */
    @Prop() icon?: string;

    /**
     * Alert badge for the menu item
     */
    @Prop() badge?: string;

    /**
     * Will change style to indicate menuitem is active when set
     */
    @Prop({ mutable: true }) is_active?: boolean;

    @Element() el: HTMLAtSidebarMenuitemElement;

    private handleKeyDown = (event: KeyboardEvent): void => {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            this.el.click();
        }
    };

    render() {
        const getClassname = classlist(
            'group text-button focus-visible:ring-active-foreground/30 font-medium relative flex w-full cursor-pointer items-center justify-between gap-8 overflow-visible rounded-md p-8 text-left transition-[background-color,color,box-shadow] duration-150 ease-in-out focus-visible:ring-2 focus-visible:outline-none',
            variantsConfig,
        );

        const classname = getClassname({
            active: this.is_active,
        });

        const getFocusIndicatorClassname = classlist(
            'pointer-events-none absolute top-0 left-0 z-0 h-full w-full rounded-md transition-colors duration-300 ease-in-out',
            focusIndicatorVariantsConfig,
        );

        const focusIndicatorClassname = getFocusIndicatorClassname({
            active: this.is_active,
        });

        return (
            <Host
                role="menuitem"
                data-state={this.is_active ? 'active' : 'inactive'}
                class={classname}
                tabIndex={0}
                onKeyDown={this.handleKeyDown}
            >
                <div class="flex items-center gap-8 truncate">
                    <slot name="menu-item-label" />
                    {this.badge && (
                        <at-badge
                            data-name="menu-badge"
                            role="presentation"
                            class="pointer-events-none absolute top-[-8px] left-[22px] z-20 !rounded-[4px] !px-[3px] !py-4 !text-[11px] !leading-[10px] !font-medium"
                            type="error"
                            impact="high"
                            rounded={false}
                            label={this.badge}
                        ></at-badge>
                    )}
                    {this.icon && (
                        <span class="material-icons text-icon-xl z-10 flex items-center justify-center p-2">
                            {this.icon}
                        </span>
                    )}
                    {this.label && (
                        <span
                            data-name="sidebar-menu-item-label"
                            class="label z-10 truncate whitespace-nowrap"
                        >
                            {this.label}
                        </span>
                    )}
                </div>
                <slot name="menu-item-actions" />
                <div
                    data-name="focus-indicator"
                    role="presentation"
                    class={focusIndicatorClassname}
                ></div>
            </Host>
        );
    }
}
